import React from 'react'

export default function Portion(props) {
    function computeIngredient(e){
        let availAmount = e.target.value;
        let ingId = e.target.id;
        let multiplier = parseFloat(availAmount)/parseFloat(props.recipe[ingId].amount);
        let ingredients = [];
        ingredients[ingId] = e.target;
        let prevIng = e.target.parentNode.parentNode.previousSibling?.children?.[0]?.children[1];
        while (prevIng){
            ingredients[prevIng.id] = prevIng;
            prevIng = prevIng.parentNode.parentNode.previousSibling?.children?.[0]?.children[1];
        }
        let nextIng = e.target.parentNode.parentNode.nextSibling?.children?.[0]?.children[1];
        while (nextIng){
            ingredients[nextIng.id] = nextIng;
            nextIng = nextIng.parentNode.parentNode.nextSibling?.children?.[0]?.children[1];
        }
        for(let i = 0; i < ingredients.length; i++){
            if (i != ingId){
                ingredients[i].value = parseFloat(props.recipe[i].amount)*multiplier;
            }
        }
        console.log(ingredients)

        console.log(availAmount,ingId,multiplier);
        //console.log(prevIng,nextIng);
    }
    return (
        <div className="container border border-2 d-flex flex-column justify-content-center">
            <div className="text-center">
                Insert Available Portion here

            </div>                {Object.keys(props.recipe).map((ingredient)=>(
                    <div key={ingredient} >
                        <form className="d-flex justify-content-between my-1">
                            <label>{props.recipe[ingredient]['ingredient']} : </label>
                            <input type={'number'} className="mx-1" name={'amount'} id={ingredient} min="1" onChange={computeIngredient}></input>
                        </form>
                    </div>
                )
                )
                }
        </div>
    )
}
