import React, {useState,useEffect} from 'react'
import Portion from './Portion';

export default function Ingredients() {
    let [numIngredients, setNumIngredients] = useState(1);
    let [recipe, setRecipe] = useState({});
    for (let i = Object.keys(recipe).length; i<numIngredients; i++){
        let ingObj = {
            ...recipe
        }
        ingObj[i] = {};
        setRecipe(ingObj);
    }
    
    function handleInput(e){
        e.preventDefault();
        let ingObj = {
            ...recipe
        }
        if (e.target.name === 'ingredient'){
            ingObj[e.target.id]['ingredient'] = e.target.value
        }
        else if(e.target.name === 'amount'){
            ingObj[e.target.id]['amount'] = e.target.value
        }
        setRecipe(ingObj);

    }
    function clickHandler(e){
        e.preventDefault();
        setNumIngredients(numIngredients+1);
    }
    useEffect(() => {
    }, [])
    return (
       <div>

              <div className="container border border-2 d-flex flex-column justify-content-center mb-5 p-2">
                <div className="text-center">Insert Ingredients here</div>
                <button onClick={clickHandler}>Add Ingredients</button>
                {Object.keys(recipe).map((ingredient)=>(
                    <div key={ingredient} className="w-100">
                        <form onChange={handleInput} className="d-flex justify-content-between my-1">
                            <label>Ingredient {parseInt(ingredient)+1}:</label>
                            <input type={'text'} className="mx-1" name={'ingredient'} id={ingredient}></input>
                            <label>Amount:</label>
                            <input type={'number'} className="mx-1" name={'amount'} id={ingredient} min="1"></input>
                        </form>
                    </div>
                )
                )
                }
            </div>
            <Portion recipe={recipe}/>
       </div>
    )
}
